.mainpage > div {
  width: 100%; }

.grid {
  min-height: 85vh;
  display: grid;
  padding: 50px;
  grid-template-columns: 1fr 50px 1fr;
  grid-template-areas: "login separator-div register";
  grid-gap: 50px;
  align-content: center; }
  .grid > div {
    width: 400px;
    text-align: center; }
  .grid .separator-div {
    grid-area: separator-div;
    justify-self: center; }
  .grid .login {
    grid-area: login;
    justify-self: end; }
  .grid .register {
    grid-area: register;
    text-align: center;
    justify-self: start; }
    .grid .register .force-left {
      width: 100%;
      text-align: left !important; }

@media (max-width: 1080px) {
  .grid {
    min-height: 80vh;
    grid-template-columns: 1fr;
    grid-template-areas: "login" "separator-div" "register";
    align-content: center; }
    .grid .separator-div {
      justify-self: center; }
    .grid .login {
      justify-self: center; }
    .grid .register {
      justify-self: center; } }
