


























































































































































.mainpage > div {
  width: 100%;
}
.grid {
  min-height: 85vh;
  display: grid;
  padding: 50px;
  grid-template-columns: 1fr 50px 1fr;
  grid-template-areas: "login separator-div register";
  grid-gap: 50px;
  align-content: center;

  & > div {
    width: 400px;
    text-align: center;
  }
  .separator-div {
    grid-area: separator-div;
    justify-self: center;
  }
  .login {
    grid-area: login;
    justify-self: end;
  }
  .register {
    grid-area: register;
    text-align: center;
    justify-self: start;

    .force-left {
      width: 100%;
      text-align: left !important;
    }
  }
}

@media (max-width: 1080px) {
  .grid {
    min-height: 80vh;
    grid-template-columns: 1fr;
    grid-template-areas:
      "login"
      "separator-div"
      "register";
    align-content: center;

    .separator-div {
      justify-self: center;
    }
    .login {
      justify-self: center;
    }
    .register {
      justify-self: center;
    }
  }
}
